.header
  position: absolute
  top: 0
  left: 0
  width: 100%
  background: rgba(255, 255, 255, 0.1)
  backdrop-filter: blur(7px)
  padding: 40px 0 15px
  z-index: 10
  &__content
    display: flex
    align-items: center
    justify-content: flex-start
    .logo-link
      display: block
      width: auto
      .logo-image
        display: block
        width: auto
        max-width: 100%
        margin: 0
    .phone-link
      display: inline-block
      margin: 0 0 0 auto
      color: #fff
      font-family: Lineatura, sans-serif
      font-weight: 700
      font-size: 14px
      line-height: 18px
      position: relative
      top: -3px