:root {
  --app-height: 100%;
}

html {
  margin: 0 !important;
}

* {
  box-sizing: border-box;
  outline: none;
}
*::before, *::after {
  box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd {
  margin: 0;
}

ul[class], ol[class] {
  padding: 0;
  list-style-type: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: inherit;
}
a[class] {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

button, label {
  cursor: pointer;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  position: relative;
  font-family: Exo2, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #FFF;
  background-color: #012238;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #012238;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #012238;
}
body::-webkit-scrollbar-track {
  background: #DFF8F6;
  border-radius: 0;
}
body {
  scrollbar-face-color: #012238;
  scrollbar-track-color: #DFF8F6;
}
body.overflow {
  overflow: hidden;
  padding-right: var(--scroll-width);
}
body.overflow .header {
  padding-right: var(--scroll-width);
}
body.overflow.menu-show:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(11, 11, 11, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 11;
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: 1300px;
  padding: 0 20px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(7px);
  padding: 40px 0 15px;
  z-index: 10;
}
.header__content {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.header__content .logo-link {
  display: block;
  width: auto;
}
.header__content .logo-link .logo-image {
  display: block;
  width: auto;
  max-width: 100%;
  margin: 0;
}
.header__content .phone-link {
  display: inline-block;
  margin: 0 0 0 auto;
  color: #fff;
  font-family: Lineatura, sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  position: relative;
  top: -3px;
}

@keyframes stars {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
@keyframes stars_hide {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
@keyframes fly {
  0%, 100% {
    transform: translate(-5px, 5px);
  }
  50% {
    transform: translate(0px, 0px);
  }
}
@keyframes fly_cloud {
  0%, 100% {
    transform: translate(-15px, -5px);
  }
  50% {
    transform: translate(0px, 0px);
  }
}
@keyframes fly_rocket {
  0% {
    transform: translate(-120vw, 120px);
  }
  100% {
    transform: translate(150vw, -50px);
  }
}
.main-slider {
  position: relative;
  width: 100%;
  height: 100vh;
}
.main-slider:before {
  content: "";
  display: block;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: radial-gradient(50% 50% at 50% 50%, #036C75 0%, #012238 100%);
}
.main-slider > .swiper-wrapper > .swiper-slide:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(../images/common/stars_1.png);
  background-position: top left;
  animation: stars 2s ease 0s infinite normal forwards;
}
.main-slider > .swiper-wrapper > .swiper-slide:after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(../images/common/stars_2.png);
  background-position: top left;
  animation: stars_hide 2s ease infinite normal forwards;
}
.main-slider > .swiper-wrapper > .swiper-slide .section-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  padding: 12.75vh 0;
  z-index: 1;
  flex: 1 1 auto;
  height: 100%;
}

.main-page .section-content {
  padding: 18.5vh 0;
}
.main-page .section-content:after {
  content: "";
  display: block;
  width: 1311px;
  height: 519px;
  position: absolute;
  top: calc(100% - 24vh);
  left: calc(-100% - 25px);
  background-image: url(../images/common/clouds.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  animation: fly_cloud 6s ease 0s infinite normal forwards;
}
.main-page__title {
  font-family: Lineatura, sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 150%;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0px 6px 4px rgba(0, 0, 0, 0.4);
}
.main-page__title span {
  color: rgb(88, 220, 227);
}
.main-page__subtitle {
  font-family: Exo2, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  margin-top: 26px;
}
.main-page__subtitle span {
  color: rgb(88, 220, 227);
}
.main-page__btn {
  display: inline-block;
  background-color: #09D3C6;
  font-family: Exo2, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #fff;
  text-transform: uppercase;
  box-shadow: 0px 8px 15px rgba(9, 211, 198, 0.55);
  border-radius: 10px;
  padding: 18px 40px;
  transition: background-color 0.3s, box-shadow 0.3s;
}
.main-page__btn:hover {
  background-color: #09D335;
  box-shadow: 0px 8px 15px rgba(9, 211, 53, 0.55);
}
.main-page .btn-row {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 35px;
}
.main-page .rocket {
  position: absolute;
  transform: translateX(-100vw);
  animation: fly_rocket 5.5s ease-in-out 0s infinite;
}
.main-page .kid {
  position: absolute;
  bottom: 1.5vh;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 5;
  animation: fly 3s ease 0s infinite normal forwards;
}