@import "settings"
@import "_mixins/_mixins"
@import "_fonts/fonts_list"
@import "_fonts/fontello"                                                                                               // if you use fontello fonts
@import "_components/reset"
@import "_components/header"
@keyframes stars
  0%, 100%
    opacity: 1
  50%
    opacity: 0
@keyframes stars_hide
  0%, 100%
    opacity: 0
  50%
    opacity: 1
@keyframes fly
  0%, 100%
    transform: translate(-5px, 5px)
  50%
    transform: translate(0px, 0px)
@keyframes fly_cloud
  0%, 100%
    transform: translate(-15px, -5px)
  50%
    transform: translate(0px, 0px)
@keyframes fly_rocket
  0%
    transform: translate(-120vw, 120px)
  100%
    transform: translate(150vw, -50px)
.main-slider
  position: relative
  width: 100%
  height: 100vh
  &:before
    content: ''
    display: block
    width: 100%
    height: 100vh
    position: fixed
    top: 0
    left: 0
    background: radial-gradient(50% 50% at 50% 50%, #036C75 0%, #012238 100%)
  & > .swiper-wrapper
    & > .swiper-slide
      &:before
        content: ''
        display: block
        width: 100%
        height: 100%
        position: absolute
        top: 0
        left: 0
        background-image: url(../images/common/stars_1.png)
        background-position: top left
        animation: stars 2s ease 0s infinite normal forwards
      &:after
        content: ''
        display: block
        width: 100%
        height: 100%
        position: absolute
        top: 0
        left: 0
        background-image: url(../images/common/stars_2.png)
        background-position: top left
        animation: stars_hide 2s ease infinite normal forwards
      .section-content
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        position: relative
        padding: 12.75vh 0
        z-index: 1
        flex: 1 1 auto
        height: 100%
.main-page
  .section-content
    padding: 18.5vh 0
    &:after
      content: ""
      display: block
      width: 1311px
      height: 519px
      position: absolute
      top: calc(100% - 24vh)
      left: calc(-100% - 25px)
      background-image: url(../images/common/clouds.png)
      background-repeat: no-repeat
      background-size: cover
      background-position: center
      animation: fly_cloud 6s ease 0s infinite normal forwards
  &__title
    font-family: Lineatura, sans-serif
    font-weight: 700
    font-size: 26px
    line-height: 150%
    text-align: center
    text-transform: uppercase
    text-shadow: 0px 6px 4px rgba(0, 0, 0, 0.4)
    span
      color: $accent-color
  &__subtitle
    font-family: Exo2, sans-serif
    font-weight: 400
    font-size: 14px
    line-height: 21px
    text-align: center
    margin-top: 26px
    span
      color: $accent-color
  &__btn
    display: inline-block
    background-color: $btn-accent-color
    font-family: Exo2, sans-serif
    font-weight: 400
    font-size: 14px
    line-height: 17px
    color: #fff
    text-transform: uppercase
    box-shadow: 0px 8px 15px rgba($btn-accent-color, 0.55)
    border-radius: 10px
    padding: 18px 40px
    transition: background-color .3s, box-shadow .3s
    &:hover
      background-color: $btn-green-color
      box-shadow: 0px 8px 15px rgba($btn-green-color, 0.55)
  .btn-row
    display: flex
    align-items: center
    justify-content: center
    margin-top: 35px
  .rocket
    position: absolute
    transform: translateX(-100vw)
    animation: fly_rocket 5.5s ease-in-out 0s infinite
  .kid
    position: absolute
    bottom: 1.5vh
    left: 0
    right: 0
    margin: 0 auto
    z-index: 5
    animation: fly 3s ease 0s infinite normal forwards

