[class^="icon-"]:before,
[class*=" icon-"]:before
  font-family: Fontello
  font-style: normal
  font-weight: normal
  speak: never
  display: block
  margin: 0
  text-decoration: inherit
  text-align: center
  font-variant: normal
  text-transform: none

/* your fontello items here */
.icon-vk:before
  content: '\e800'
.icon-btn_arrow:before
  content: '\e801'
.icon-check:before
  content: '\e802'
.icon-close:before
  content: '\e803'
.icon-file:before
  content: '\e804'
.icon-ok:before
  content: '\e805'
.icon-play:before
  content: '\e806'
.icon-yt:before
  content: '\f16a'